#loading-screen {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000000;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 1;
    transition: opacity 1s ease-in-out; /* Transición de opacidad */
    z-index: 9999;
  }

  #loading-screen img {
    max-width: 256px; /* Ajusta el tamaño de la imagen según tus necesidades */
  }

/* Estilos para el contenedor de la escena */
        #canvasContainer {
            position: relative;
            width: 100%;
            height: 100vh; /* Ajusta la altura según tus necesidades */
        }

        /* Estilos para el botón */
        #toggleGreenPolygon {
            position: absolute;
            top: 20px; /* Ajusta la posición vertical según tus necesidades */
            right: 20px; /* Ajusta la posición horizontal según tus necesidades */
            z-index: 10; /* Asegura que el botón esté en un nivel superior a la escena */
        }

        #resetCameraButton {
          position: absolute;
          top: 20px; /* Ajusta la posición vertical según tus necesidades */
          left: 20px; /* Ajusta la posición horizontal según tus necesidades */
          z-index: 10; /* Asegura que el botón esté en un nivel superior a la escena */
      }